import React, { Component } from 'react';
import {ReviewComments,ProfileImage} from '../../styles/init';

/**
 * @param string image
 * @param string name
 * @param string title
 * @param string review
 */

export default class Reviews extends Component {
    render() {
        const { image,name,title,review } = this.props;
        return (
            <ReviewComments>
                <ProfileImage>
                  <img src={image} alt="pp"/>
                </ProfileImage>
                <label>{name}</label>
                <p className="linkblue">{title}</p>
                <p>{review}</p>
            </ReviewComments>
        )
    }
}
